.about{

  .contact-info{
    width: 20%;
  }

  .circle1 {
   width: 300px;
   height: 350px;
   background: $color2;
   -moz-border-radius: 200px;
   -webkit-border-radius: 200px;
   /*border-radius: 250px;*/
   border-top-left-radius: 0px;
   border-top-right-radius:200px;
   border-bottom-left-radius: 0px;
   border-bottom-right-radius:200px;
   margin-left: 0;
  }

  .circle2 {

  padding: 30px;
  width: 300px;
  height: 350px;
  background: $color2;
  -moz-border-radius: 200px;
  -webkit-border-radius: 200px;
  /*border-radius: 250px;*/
  border-top-left-radius: 0px;
  border-top-right-radius:200px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius:200px;
  margin-left: 0;

  }

.content-area{
    margin-left: 25%;
    width: 75%;
    background: $color1;
    position: absolute;
    top: 0;
    left:0;
    bottom:0;
    height: 100%;
    overflow: hidden;

    h1{
      font-weight: 700;
      font-size: 1.3rem;
      text-transform: uppercase;
      margin-bottom: 20px;
    }

    .schoolSection{
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 30px;
      grid-row-gap: 10px;
      max-width: 1000px;

      .schoolBox{
        padding: 15px;
        transition: all 0.5s ease-in-out;

        &:hover{
          -webkit-box-shadow: 0px 10px 8px 5px rgba(0,0,0,0.10);
          -moz-box-shadow: 0px 10px 8px 5px rgba(0,0,0,0.10);
          box-shadow: 0px 10px 8px 5px rgba(0,0,0,0.10);
        }

        h5{
          font-weight: 550;
          font-size: 1rem;
          margin-bottom: 1rem;
        }
        p{
          font-weight: 300;
          font-size: 0.8rem;
          margin-bottom: 0.3rem;
        }
      }/*END OF SCHOOLBOX*/
    }/*END OF SCHOOLSECTION*/

    .my-descr-section{

      display: inline-block;

      .my-descr{
        position: absolute;
        left: 250px;
        margin-top:15%;
        width: 60%;
      }

    }
  }
}
