// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {

  .home{

  .content-area{

      /*padding: 0 0 0 25%;*/


  .center-img{
    position:absolute;
    justify-content: center;
    margin-top: 25%;
    width: 65%;
    height:50%;
    background: url("breasha.png") no-repeat center center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    border-radius: 500px;

    -webkit-box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 1);
    -moz-box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 1);
    box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.3);
}



      .my-descr-section{

        display:block;

        .my-descr{
          position: fixed;
          left: 250px;
          margin-top:85%;
          width: 60%;
          text-align: center;
          justify-content: center;

          h1{
            font-size: 5.3rem;
            font-weight: 525;
            line-height: 1.2;
            margin: 0 0 2.7rem 0;
            span{
              color:$color2;
            }
          }

          h5{
            font-size: 3.1rem;
            margin: 0 0 4.0rem 0;
          }
          p{
            color: $color5;
            font-weight: 300;
            line-height: 1.4;
            margin: 0 0 6rem 0;
            font-size: 2.1rem;
          }
        }

      }
    }
 }

}

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767.98px) {

  .home{

    .contact-info{
      padding: 40px;
    }

  .content-area{
      margin-left: 35%;
      width: 65%;
      background: $color1;
      position: absolute;
      top: 0;
      left:0;
      bottom:0;
      height: 100%;
      /*padding: 0 0 0 25%;*/

  .img-container{
    display: inline-block;
    width: 25%;
    position: relative;
  }

      .my-descr-section{

        display: inline-block;

        .my-descr{
          position: absolute;
          left: 250px;
          margin-top:15%;
          width: 60%;

          h1{
            font-size: 2.3rem;
            font-weight: 525;
            line-height: 1.2;
            margin: 0 0 1.7rem 0;
            span{
              color:$color2;
            }
          }

          h5{
            font-size: 1.1rem;
            margin: 0 0 1.0rem 0;
          }
          p{
            color: $color5;
            font-weight: 300;
            line-height: 1.4;
            margin: 0 0 2rem 0;
          }
        }

      }
    }
  }

 }
