// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {

  .my-info{

      padding: 100px;
      position: absolute;
      bottom: 40px;

      h4{
        font-size: 0.9rem;
        color: $color1;
        margin: 0.4rem 0;
      }
      p{
        font-size: 0.8rem;
        font-weight: 300;
        color: $color1;
        margin: 0.2rem 0 1.3rem;
      }

  .icon-area{
    a{
      color: $color1;
      text-decoration: none;
      font-size: 1rem;
      padding: 0 8px;

      &:hover{
        color: fade-in($color6, 0.5);
      }

      &:first-child{
        padding: 0 8px 0 0;
      }
    }

  }
  }

  .circle2 {

  .logo{
    max-width: 35%;
    height: auto;
    background: #EA78B4;
    border-radius: 0 30px 30px 0px;
    padding-right: 20px;
  }

  }

}/*END OF MEDIA*/

