// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {

  .contact-info{
    padding: 40px;
    width: 35%;
    position: absolute;
    top: 0;
    bottom: 0;
    left:0;
  }

  .my-info{
      padding: 40px;
      position: absolute;
      bottom: 20px;

      h4{
        font-size: 0.9rem;
        color: $color1;
        margin: 0.4rem 0;
      }
      p{
        font-size: 0.8rem;
        font-weight: 300;
        color: $color1;
        margin: 0.2rem 0 1.3rem;
      }

  .icon-area{
    a{
      color: $color1;
      text-decoration: none;
      font-size: 1rem;
      padding: 0 8px;

      &:hover{
        color: fade-in($color6, 0.5);
      }

      &:first-child{
        padding: 0 8px 0 0;
      }
    }

  }
  }

  .circle1 {
   width: 440px;
   height: 350px;
   background: $color2;
   -moz-border-radius: 250px;
   -webkit-border-radius: 250px;
   /*border-radius: 250px;*/
   border-top-left-radius: 0px;
   border-top-right-radius:250px;
   border-bottom-left-radius: 0px;
   border-bottom-right-radius:250px;
   margin-left: 0;
  }

  .circle2 {

  padding: 30px;
  width: 440px;
  height: 350px;
  background: $color2;
  -moz-border-radius: 250px;
  -webkit-border-radius: 250px;
  /*border-radius: 250px;*/
  border-top-left-radius: 0px;
  border-top-right-radius:250px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius:250px;
  margin-left: 0;

  .logo{
    max-width: 40%;
  }

  }



 }
