.work{

  .contact-info{
    width: 20%;
  }

  .circle1 {
   width: 300px;
   height: 350px;
   background: $color2;
   -moz-border-radius: 200px;
   -webkit-border-radius: 200px;
   /*border-radius: 250px;*/
   border-top-left-radius: 0px;
   border-top-right-radius:200px;
   border-bottom-left-radius: 0px;
   border-bottom-right-radius:200px;
   margin-left: 0;
  }

  .circle2 {

  padding: 30px;
  width: 300px;
  height: 350px;
  background: $color2;
  -moz-border-radius: 200px;
  -webkit-border-radius: 200px;
  /*border-radius: 250px;*/
  border-top-left-radius: 0px;
  border-top-right-radius:200px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius:200px;
  margin-left: 0;

  }

.content-area{
    margin-left: 25%;
    width: 75%;
    background: $color1;
    position: absolute;
    top: 0;
    left:0;
    bottom:0;
    height: 100%;
    overflow: hidden;

    .work-section{
      margin-bottom: 40;
    }

    h1{
      font-weight: 700;
      font-size: 1.3rem;
      text-transform: uppercase;
      margin-bottom: 20px;
    }

    .projectSection{
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 1px;
      grid-row-gap: 0px;

      .projectBox{
        position: relative;
        padding: 15px;
        transition: all 0.5s ease-in-out;
        margin: 15px;

        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;

        -moz-border-radius: 10px;
        -webkit-border-radius: 10px;
        /*border-radius: 250px;*/
        border-top-left-radius: 10px;
        border-top-right-radius:10px;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;

        height: 250px;
        width: 330px;

        &:hover{
          -webkit-box-shadow: 10px 15px 8px 1px rgba(0,0,0,0.15);
          -moz-box-shadow: 10px 15px 1px 1px rgba(0,0,0,0.15);
          box-shadow: 10px 15px 8px 1px rgba(0,0,0,0.15);/*TOP, BOTTOM, LEFT, RIGHT*/
        }

        .pinkBg{
          background: $color2;
          background-image: -webkit-radial-gradient(top, circle cover, #FF3094 10%, #a71f60 70%);
          background-image: -moz-radial-gradient(top, circle cover, #FFC1EF 0%, #FFC1EF 90%);
          background-image: -o-radial-gradient(top, circle cover, #941652 60%, #FFC1EF 90%);
          background-image: radial-gradient(top, circle cover, #FFC1EF 100%, #FFC1EF 80%);

          position: absolute;
          top: 0;
          bottom: 0;
          right: 0;
          left: 0;
          opacity: 0;
          transition: all .3s ease-in-out;

          -moz-border-radius: 10px;
          -webkit-border-radius: 10px;
          /*border-radius: 250px;*/
          border-top-left-radius: 10px;
          border-top-right-radius:10px;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius:10px;

          &:hover {
            opacity: 0.9;

            /*ANIMATION*/
                .anim-typewriter {
                      animation: typewriter 3s steps(40) 1s 1 normal both,
                      blinkTextCursor 700ms steps(60) infinite normal;
                }

                @keyframes typewriter {
                      from {
                            width: 0;
                      }
                      to {
                            width: 16em;
                      }
                }

                @keyframes blinkTextCursor {
                      from {
                            border-right-color: rgba(255, 255, 255, 255.75);
                      }
                      to {
                            border-right-color: transparent;
                      }
                }

          }

          .projectInfo{
            padding: 20px;
            position: absolute;
            bottom: 0;
            width: 100%;
            font-family: 'Anonymous Pro', monospace;
            color: $color1;


            .projectdets{
              font-size: 0.8rem;
              text-transform: capitalize;
              margin-bottom: 5px;
              margin-left: 0px;
            }

            .project-title{
              position: relative;
              font-size: 1rem;
              text-transform: uppercase;
              font-weight: 700;
              margin-left: 0px;
            }

            /*POSITION THE ICONS NEXT TO EACH OTHER FROM THE RIGHT*/

            .fa-eye{
              position: absolute;
              bottom: 60;
              left: 250px;
            }
            .fa-code{
              position: absolute;
              bottom: 60;
              left: 200px;
            }

            a{
              color: $color6;
              text-decoration: none;
            }


          }/*END OF PROJECT INFO*/

        }

        h5{
          font-weight: 550;
          font-size: 1rem;
          margin-bottom: 1rem;
        }
        p{
          font-weight: 300;
          font-size: 0.8rem;
          margin-bottom: 0.3rem;
        }
      }/*END OF SCHOOLBOX*/
    }/*END OF SCHOOLSECTION*/
    .line {
      position: relative;
      top: 50%;
      width: 1em;
      margin: 0 auto;
      border-right: 2px solid rgba(255, 255, 255, 0.75);
      font-size: 180%;
      text-align: left;
      white-space:nowrap;
      overflow: hidden;transform: translateY(-50%);
    }
  }
}
