.contact{

  /* Mixins */
  @mixin gradientBg($direction, $startColor, $endColor) {
  background: $startColor;
  background: -moz-linear-gradient($direction,  $startColor 0%, $endColor 80%,$endColor 100%);
  background: -webkit-linear-gradient($direction,  $startColor 0%, $endColor 80%,$endColor 100%);
  background: linear-gradient($direction,  $startColor 0%, $endColor 80%,$endColor 100%);
  }

  .contact-info{
    padding: 40px;
    width: 35%;
  }

.content-area{
    margin-left: 35%;
    width: 65%;
    background: $color1;
    position: absolute;
    top: 0;
    left:0;
    bottom:0;
    height: 100%;
    /*padding: 0 0 0 25%;*/

  .form{
    position: absolute;
    padding-top: 20px;
    padding-left: 30px;
    left: 0px;

    /*STYLE THE INPUT BOXES*/
    input[type="text"], input[type="email"], textarea{
      background: transparent;
      border: none;
      border-bottom: 1px solid $color3;
      width: 100%;
      padding-bottom: 30px;
      margin-bottom: 1.2rem;
      font-size: 0.9rem;
      line-height: 1.2;
      color: $color5
    }

    button[type="submit"]{
      background: $color2;

      border: none;
      -webkit-border-radius: 30px;
      -moz-border-radius: 30px;
      border-radius: 30px;
      text-align:center;
      color: $color1;
      padding: 10px 24px;
      display: inline-block;
      text-transform: uppercase;
      letter-spacing: 1px;
      margin-right: 25px;
      font-size: 0.7rem;
      text-decoration: none;

      /*SHADOW*/
      box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 8px 10px rgba(0,0,0,0.22);
      transition: width 3s;
      transition-timing-function: cubic-bezier(0.1, 0.7, 1.0, 0.1);

      &:hover{
        color: white;
        @include gradientBg(85deg, #80004A, #A60061);
        cursor: pointer;
        border: none;
        -webkit-animation: none;

        /*PUSH SHADOW*/
        box-shadow: 0 4px 8px rgba(0,0,0,0.10), 0 2px 4px rgba(0,0,0,0.24);
      }
    }
}

    .my-descr-section{

      display: inline-block;

      .my-descr{
        position: absolute;
        left: 50px;
        margin-top:12%;
        width: 60%;

        h1{
          font-size: 2.3rem;
          font-weight: 525;
          line-height: 1.2;
          margin: 0 0 1.7rem 0;
          span{
            color:$color2;
          }
        }

        h5{
          font-size: 1.1rem;
          margin: 0 0 1.0rem 0;
        }
        p{
          color: $color5;
          font-weight: 300;
          line-height: 1.4;
          margin: 0 0 2rem 0;
        }
      }

    }
  }
}
