@import"reset";
@import"variables";
@import"infoSection";
@import"homepg";
@import"aboutpg";
@import"workpg";
@import"contactpg";
@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);


/*HELPFS WITH RESPONSIVE DESIGN ETC*/
/*
*{
  box-sizing: border-box;
} */
*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.gradient-1 {
 background: #941652;
 background-image: -webkit-radial-gradient(top, circle cover, #941652 20%, #a71f60 70%);
 background-image: -moz-radial-gradient(top, circle cover, #FFC1EF 0%, #FFC1EF 90%);
 background-image: -o-radial-gradient(top, circle cover, #941652 60%, #FFC1EF 90%);
 background-image: radial-gradient(top, circle cover, #FFC1EF 100%, #FFC1EF 80%);
}

html, body{
  font-size: 18px;
  background: $color1;
  font-family: 'Arimo', sans-serif; /*google font*/
  height:100%;
}

.menu-btn{
  width: 20px;
  float: right;
  cursor: pointer;
  padding-right: 12%;
  padding-top: 8%;
}

/*COLOR OPACITY*/
@mixin bg-opacity($color, $opacity) {
  background: $color;
  background: rgba($color, $opacity);
}

nav{
  position: absolute; /*It sits ontop of all the other elements*/
  @include bg-opacity($color1, 0.96);
  padding: 2em;
  width: calc(100% - 4em);
  height: 100vh; /*calc(100vh - 4em)*/
  z-index: 12;
  display: none;
  transform: scaleX(0);
  transform-origin: right;

    .close-btn{
      width: 20px;
      float: right;
      cursor: pointer;
      padding-right: 4%;
      padding-top: 5%;
      opacity: 0;
      animation: show-x 1s 1.3s forwards;


    }

    ul{

      margin: 20% auto 0 auto;
      padding: 0;
      display: grid;
      grid-template-columns: repeat(4, 25%);
      width: 80%;
      text-align: center;
    }

    a{
      text-decoration: none;
      color: $color2;
      font-weight: bold;
      letter-spacing: 0.11rem;
      font-size: 1.1rem;
      transition: all .2s ease-in-out;

      &:hover{
        color: fade-in($color4, 0.5);
        border-bottom: 1px ridge grey;
      }

      &:first-child{
        padding: 0 8px 0 0;
      }
    }


    span{
      color: $color5;
      display: block;
      font-size: .75em;
      margin-top: 20px;
    }

    li{
       opacity: 0;
    }

    /*animation*/
    animation: slide-menu 1s ease-in forwards;
    li:nth-of-type(1){
      animation: menu-item-anim .6s forwards 1s ease-in-out;
    }
    animation: slide-menu 1s ease-in forwards;
    li:nth-of-type(2){
      animation: menu-item-anim .6s forwards 1.2s ease-in-out;
    }
    animation: slide-menu 1s ease-in forwards;
    li:nth-of-type(3){
      animation: menu-item-anim .6s forwards 1.4s ease-in-out;
    }
    animation: slide-menu 1s ease-in forwards;
    li:nth-of-type(4){
      animation: menu-item-anim .6s forwards 1.6s ease-in-out;
    }


}/*END OF NAV*/

.grow {
transition: all .2s ease-in-out;
}

.grow:hover {
transform: scale(1.1);
}



/*KEY FRAMES*/

/*MENU ENTRANCE*/
@keyframes slide-menu{
  from{
    transform: scaleX(0);
  }to{
    transform: scaleX(1);
  }
}

@keyframes show-x{
  from{
    opacity: 0;
  }to{
    opacity: 1;
  }
}

