    .show-menu{
      display: block;
    }

.active-btn{
background: $color2;
border: 2px solid $color2;
-webkit-border-radius: 30px;
-moz-border-radius: 30px;
border-radius: 30px;
text-align:center;
color: $color1;
padding: 12px 28px;
display: inline-block;
text-transform: uppercase;
letter-spacing: 1px;
margin-right: 25px;
font-size: 0.8rem;
text-decoration: none;
transition: $btnTransitions;


/*PULSATE ANIMATION*/
cursor: pointer;
box-shadow: 0 0 0 0 rgba(#941652, .5);
-webkit-animation: pulse 3s infinite;

&:hover{
  background: $hover1;
  border: 2px solid $hover1;
  -webkit-animation: none;
  box-shadow: 0 2px 2px rgba(0,0,0,0.16), 0 1px 2px rgba(0,0,0,0.1);
}

}

/* Mixins */
@mixin gradientBg($direction, $startColor, $endColor) {
background: $startColor;
background: -moz-linear-gradient($direction,  $startColor 0%, $endColor 80%,$endColor 100%);
background: -webkit-linear-gradient($direction,  $startColor 0%, $endColor 80%,$endColor 100%);
background: linear-gradient($direction,  $startColor 0%, $endColor 80%,$endColor 100%);
}

@mixin transform($property) {
  -webkit-transform: $property;
  -ms-transform: $property;
  transform: $property;
}

@-webkit-keyframes pulse {
  0% {
   @include transform(scale(0.9));
 }
}

@-webkit-keyframes pulse {
  0% {
    @include transform(scale(0.9));
  }
  70% {
    @include transform(scale(1));
    box-shadow: 0 0 0 20px rgba(#941652, 0);
  }
    100% {
    @include transform(scale(0.9));
    box-shadow: 0 0 0 0 rgba(#941652, 0);
  }
}
/*END OF PULSATE ANIMATION*/

.inactive-btn{
    border: 2px solid $color2;
    color: $color2;
    border-radius:30px;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    border-radius: 30px;
    text-align:center;
    font-weight:bold;
    padding:12px 28px;
    display: inline-block;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.8rem;
    text-decoration: none;
    position:relative;

    /*SHADOW*/
    box-shadow: 0 4px 8px rgba(0,0,0,0.1), 0 4px 5px rgba(0,0,0,0.2);
    transition: width 3s;
    transition-timing-function: cubic-bezier(0.1, 0.7, 1.0, 0.1);

&:hover{
  color: $color1;
  	@include gradientBg(85deg, #80004A, #A60061);
    box-shadow: 0 1px 4px rgba(0,0,0,0.10), 0 2px 4px rgba(0,0,0,0.24);
}

  }




